.content {
    display: grid;
    width: 1200px;
    margin: 30px auto;
    /* height: 463px; */
    grid-template-columns: 35% 65%;
    padding-top: 20px;
    margin-top: 10px;
    padding-bottom: 20px;
    margin-bottom: 10px;
    /* grid-auto-rows: minmax (500px, auto); */
    /* grid-auto-rows: 100px; */
    /* grid-auto-columns: 100px; */
    /* background-color: blue; */
    /* padding-left: 4px;
    padding-right: 4px; */
    border-top: 2px solid #ECEEF2;
    border-bottom: 2px solid #ECEEF2;
}

.btnBox {
    display: grid;
    justify-items: start;
    align-items: start;
    grid-column: 1/2;
    grid-row: 3/4;
}

.privacyPolicy {
    display: grid;
    grid-column: 1/3;
    grid-row: 4/5;
    justify-items: start;
    align-self: center;
    padding-left: 10px;
    padding-bottom: 10px;
}

.privacyPolicy a {
    color: #259B00;
}

.btn {
    /* height: 39;
    width: 118; */
    margin-top: 15px;
    margin-bottom: 15px;
    margin-left: 10px;
    padding: 10px 20px 10px 20px;
    border-radius: 6px;
    background-color: #259B00;
    color: #ffffff;
    font-size: 16px;
    border: none;
}

.btnBox :hover {
    background-color: #AE0018;
}

.btnBox :active {
    background-color: #AE0018;
}

.leftColumn {
    display: grid;
    grid-column: 1/2;
    grid-template-rows: 15% 85%;
    /* padding-left: 4px; */
    /* background-color: #259B00; */
}

.leftColumn__leftHeader {
    display: grid;
    font-size: 30px;
    justify-self: start;
    align-self: start;
    padding-bottom: 7px;
    color: #259B00;
    border-bottom: 2px solid #ECEEF2;

}

.leftColumn__leftText {
    display: grid;
    /* justify-self: start; */
    /* align-self: center; */
    text-align: start;
    padding-top: 10px;

}

.leftColumn__leftimg {
    display: grid;

}

.rightColumn {
    display: grid;
    grid-column: 2/3;
    grid-template-columns: 50% 50%;
    align-items: start;
    grid-template-rows: 54px auto auto;
    grid-auto-rows: auto;
    /* grid-row-gap: 4px; */
    /* padding-left: 4px; */
    /* background-color: #259B00; */
}

.rightColumn__rightRow {
    display: grid;
    grid-column: 1/3;
    align-self: start;
    /* background-color: rgb(54, 5, 133); */

}

.textBox1 {
    display: grid;
    height: 41px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
    margin-bottom: 3px;
    /* background-color: bisque; */
}

.textBox2 {
    display: grid;
    height: 41px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
    margin-bottom: 3px;
    /* background-color: bisque; */
}

.dialogs_items {
    padding: 10px;
    resize: none;
    font-size: 16px;
    border-color: #259B00;
    border-radius: 6px;
    outline: none;

}

.textBox1 :focus {
    background-color: #ECEEF2;
}

.textBox2 :focus {
    background-color: #ECEEF2;
}

.textBox3 :focus {
    background-color: #ECEEF2;
}

.textBox3 {
    display: grid;
    height: 142px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
    margin-bottom: 3px;
    /* background-color: bisque; */
}