.itemBox {
    display: grid;
    grid-row: 2/3;
    grid-template-columns: 1fr 1fr;
    margin-top: 25px;
    margin-bottom: 25px;
    grid-column-gap: 28px;
}

.itemBoxLeft {
    display: grid;
    grid-row-gap: 13px;
    grid-template-rows: 1fr 1fr;
    justify-content: end;
    align-items: end;
}

.itemBoxRight {
    display: grid;
    grid-row-gap: 13px;
    grid-template-rows: 1fr 1fr;
    justify-content: start;
    align-items: end;
}

.img {
    background-color: rgb(6, 6, 6);
    height: 240px;
    width: 240px;
    border-radius: 50%;
    margin-left: 26px;
    margin-top: 26px;
    box-shadow: 2px 2px 3px #90909083;
}