.sliderImg {
    
    width: 100%;
    height: 400px;
    /* height: 500px; */
    position: absolute;
    pointer-events: none;
    animation: moveImage 120s infinite linear;
    background-repeat: repeat-x;
    background-size: contain;
    background-image: url(./../../../../../public/3in1.svg);
    z-index: 1;
    /* background-color: #ECEEF2; */
    /* object-fit: cover; */
}

@keyframes moveImage {
    0% {
        background-position: 0 center;
    }

    100% {
        background-position: -10000px center;
    }
}