.item {
    display: grid;
    width: 100%;
    margin-left: 10px;
    margin-right: 10px;
    text-transform: uppercase;
    align-items: center;
}

.item a {
    display: grid;
    align-content: center;
    justify-content: center;
    grid-column: 1/2;
    height: 90px;
    color: #000000;

}

.item a:hover {
    color: #AE0018;
}

.item a.activeLink {
    color: #AE0018;
}

.item svg {
    display: grid;
    width: 18px;
    height: 18px;
    margin-left: 15px;
    grid-column: 2/3;
    fill: #000000;
}

.item:hover svg {
    fill: #AE0018;
}

