.itemBox {
    display: grid;
    /* align-items: center;
    justify-items: center; */
    grid-template-rows: 222px 58px;
    width: 410px;
    box-shadow: 2px 2px 3px #90909083;
    border-radius: 18px 18px 6px 6px;
}

