body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-style: normal;
  text-decoration: none;
}

body a {
  text-decoration: none;
}

.roboto-regular,
.roboto-thin,
.roboto-light,
.roboto-medium,
.roboto-bold,
.roboto-black {
  font-family: "Roboto", sans-serif;
  font-style: normal;
}

.roboto-thin,
.roboto-light,
.roboto-regular,
.roboto-medium,
.roboto-bold,
.roboto-black {
  font-weight: 100;
  font-weight: 300;
  font-weight: 400;
  font-weight: 500;
  font-weight: 700;
  font-weight: 900;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-size: 50px;
  font-weight: 400;
  margin-block: 0;
}

h2 {
  font-size: 40px;
  font-weight: 400;
  margin-block: 0;
}