.content {
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto;
    row-gap: 20px;
    align-items: center;
}

.itemHead {
    display: grid;
    grid-row: 1/2;
    grid-column: 2/3;
    justify-content: center;
    /* grid-column-gap: 28px; */
}

.itemText {
    display: grid;
    justify-content: center;
    grid-row: 2/3;
    grid-column: 2/3;
    /* grid-column-gap: 28px; */
    margin-left: 30px;
    font-size: 24px;
    font-weight: 300;
}

.img {
    display: grid;
    grid-row: 1/3;
    grid-column: 1/2;
    align-content: center;


}

.img img {
    background-color: rgb(6, 6, 6);
    height: 222px;
    width: 410px;
}