.footer {
    background-color: rgb(0, 0, 0);
    display: grid;
}

.box {

    /* padding: 10px; */
    display: grid;
    /* grid-column: 1/3; */
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    border-top-width: 1px;
    border-top-style: solid;
    border-top-color: rgb(224, 224, 224);
    align-content: center;
    height: 240px;

}

.item {
    display: grid;
    grid-row: 1/2;
    color: #ffffff;
    align-content: center;
}

.disclaimer {
    display: grid;
    /* grid-row: 2/3; */
    /* grid-column: 1/6; */
    justify-content: center;
    align-content: end;
    color: #ffffff;
    margin-bottom: 10px;
    font-style: italic;
}

.footer img {
    padding-left: 25px;
    margin-right: 25px;
    width: 245px;
    align-content: center;
    grid-column: 1/2;
    grid-row: 1/2;
    /* align-items: center; */
    /* height: 90px;
    width: 120px; */
    /* background-color: rgb(39, 20, 206); */
}