.contentBg {
    display: grid;
    width: auto;
    background-color: #ffffff;
}

.content {
    display: grid;
    /* grid-row: 2fr 1fr; */
    text-decoration: none;

}

.itemBox {
    display: grid;
    /* grid-row: 2/3; */
    grid-template-columns: 1fr 1fr;
    margin-top: 25px;
    margin-bottom: 25px;
    grid-column-gap: 28px;
}

.itemBoxLeft {
    display: grid;
    grid-row-gap: 13px;
    grid-template-rows: 1fr 1fr;
    justify-content: end;
    align-items: end;
}

.itemBoxRight {
    display: grid;
    grid-row-gap: 13px;
    grid-template-rows: 1fr 1fr;
    justify-content: start;
    align-items: end;
}

