.content a {
    color: #000000;
    text-decoration: none;
}

.content a:hover {
    color: #AE0018;
}

.position {
    font-size: 18px;
    font-weight: 300;
}

.name {
    font-size: 18px;
    font-weight: 500;
}

.phone {
    font-size: 18px;
    font-weight: 300;
}

.phone :hover {
    color: #AE0018;
}


.email {
    font-size: 18px;
    width: 300;
    text-decoration: none;
}

.email :hover {
    color: #AE0018;
}