.item {
    display: grid;
    justify-content: start;
    align-content: center;
    /* grid-auto-columns: 50px; */
    padding-left: 25px;
    /* padding-bottom: 25px; */
    z-index: 2;
    text-transform: uppercase;
    /* font-size: 30px; */
    font-style: italic;
    font-weight: 400;
    /* margin-right: 20px; */
    grid-column: 1/2;
    /* padding-right: 20px; */
    border-right-width: 2px;
    border-right-style: solid;
    border-right-color: #AE0018;

    font-weight: 500;
    height: 100%;
    font-size: 40px;
}