.content {
    display: grid;
    grid-template-rows: 50% 50%;
    grid-template-columns: 40% 60%;
    background-color: #ECEEF2;
    height: 400px;
    grid-template-columns: 650px 70%;
}

.imgBox {
    display: grid;
    grid-column: 2/3;
    grid-row: 1/3;
    height: 400px;
    justify-content: end;
    align-content: start;
    overflow: hidden;
}

.img {
    display: grid;
    grid-column: 2/3;
    grid-row: 1/3;
    width: 100%;
    height: 1100px;
    object-fit: cover;
    align-content: end;
    margin-top: -100px;
    padding-right: -50px;
    /* margin-left: 50px; */
    z-index: 3;

}

