.item {
    display: grid;
    width: 100%;
    text-transform: uppercase;
}

.item a:hover {
    color: #AE0018;
}

.item a {
    display: grid;
    align-content: center;
    justify-content: center;
    margin-left: 10px;
    margin-right: 10px;
    height: 90px;
    color: #000000;
}

.item a.activeLink {
    color: #AE0018;
}